import axios from 'axios';

export default class VtexCatalogService {
  /**
   * @param  {String} origin='' Origin example => https://www.example.com
   */
  constructor(origin = '') {
    this.api = axios.create({
      baseURL: `${origin}/api/catalog_system`,
    });
  }

  /**
   * This function is a abstraction of VTEX Search API for get and return products by categoryID (Parent) and subcategoryID (child)
   * and return an array of products, also you can order the products with orderBy param, fetch max 50 products per request.
   *
   * For more information go to the follow link:
   * https://documenter.getpostman.com/view/845/vtex-search-api/Hs43?version=latest#e0ec3923-faf7-5049-8634-f6d617ce5f67
   *
   * @param  {String|Number} categoryID
   * @param  {String|Number} subcategoryID
   * @param  {String|Number} _from=0
   * @param  {String|Number} _to=49
   * @param  {Array} orderBy=['OrderByNameASC']
   * @return {Array} Array of products.
   */
  async getProductsByCategories(
    categoryID,
    subcategoryID,
    _from = 0,
    _to = 49,
    orderBy = ['OrderByNameASC']
  ) {
    try {
      const params = new URLSearchParams();

      params.append('_to', _to);
      params.append('_from', _from);
      params.append('fq', `C:/${categoryID}/${subcategoryID}`);

      orderBy.forEach(order => params.append('O', order));

      return await this.api.get('/pub/products/search', { params });
    } catch (error) {
      console.error(
        'Error in VtexCatalogService on method getProductsByCategories',
        error
      );
    }

    return;
  }

  /**
   * This function get and return all products by categoryID (Parent) and subcategoryID (child)
   * and return an array of products.
   * @param  {String|Number} categoryID
   * @param  {String|Number} subcategoryID
   * @return {Array} Array of products.
   */
  async getAllProductsByCategories(categoryID, subcategoryID) {
    try {
      let products = [];
      let initial = 0;
      let end = 49;

      while (true) {
        const { data, status } = await this.getProductsByCategories(
          categoryID,
          subcategoryID,
          initial,
          end
        );

        initial = end + 1;
        end = end * 2 + 1;

        products = [...products, ...data];

        if (status === 200) {
          break;
        }
      }

      return products;
    } catch (error) {
      console.error(
        'Error in VtexCatalogService on method getAllProductsByCategories',
        error
      );
    }

    return;
  }

  /**
   * This function get and return all categories of the category tree.
   * @return {Array} Array of categories.
   */
  async getAllCategories() {
    try {
      return await this.api.get('/pub/category/tree/1');
    } catch (error) {
      console.error(
        'Error in VtexCatalogService on method getAllCategories',
        error
      );
    }

    return;
  }

  /**
   * @param  {String|Number} productId The id of the product
   * @return {Object} Product Object with SKUS Variations.
   */
  async getProductVariations(productId) {
    try {
      return await this.api.get(`/pub/products/variations/${productId}`);
    } catch (error) {
      console.error(
        'Error in VtexCatalogService on method getProductVariations',
        error
      );
    }

    return;
  }
}
