// Components
import CustomSelect from 'Components/b8one.customSelect.js';

// Services
import CartService from 'Services/b8one.cartService.js';
import VtexCatalogService from 'Services/b8one.vtexCatalogService.js';

class Address {
  constructor() {
    this.cartService = new CartService();
    this.vtexCatalogService = new VtexCatalogService();

    this.customSelectInstances = [];
    this.isToAddressUnknown = false;

    this.init();
  }

  async init() {
    this.eventHandlerBinds();
    this.createCustomSelect();
    this.setHandledButtonUnknownAddressClick();

    await this.getAddressData();
  }

  eventHandlerBinds = () => {
    this.setHandledButtonSave();
    this.setHandledFormsChange();
  };

  setHandledButtonSave() {
    const buttonSave = document.querySelector('.js-button-save');
    const buttonNext = document.querySelector('.button--primary');

    buttonSave.addEventListener('click', async e => {
      document.dispatchEvent(window.initSaveCustomDataEvent);

      await this.saveData();

      document.dispatchEvent(window.endSaveCustomDataEvent);
    });

    buttonNext.addEventListener('click', async e => {
      buttonNext.classList.toggle('js-loading');
      await this.saveData();

      if (this.validateForms()) {
        buttonNext.classList.toggle('js-loading');
        window.location.assign('/seguro-info');
      }

      buttonNext.classList.toggle('js-loading');
    });
  }

  getAddressFieldsForOrderInfo() {
    const fieldsWithDefaultData = {
      to_address_name: 'Sem Endereço',
      to_address_number: 'Sem Numero',
      to_address_county: 'Sem Bairro',
    };

    const orderInfoAddressFields = [...document.forms]
      .filter(form => form.classList.contains('address__form'))
      .reduce((obj1, form) => {
        const addressFields = [...form.elements]
          .filter(({ type, classList, value, id }) => {
            if (classList.contains('js-address-complement-to') && value === '')
              return false;

            if (!id) return false;

            return type !== 'button';
          })
          .reduce((obj2, { id, value, checked }) => {
            if (
              this.isToAddressUnknown &&
              fieldsWithDefaultData.hasOwnProperty(id)
            ) {
              value = fieldsWithDefaultData[id];
            }

            return {
              ...obj2,
              [id]: checked ? checked : value,
            };
          }, {});

        const key = form.classList.contains('js-address-from')
          ? 'from_address'
          : 'to_address';

        return {
          ...obj1,
          [key]: addressFields,
        };
      }, {});

    return orderInfoAddressFields;
  }

  getAddressForOrderForm(orderInfoAddressFields) {
    const { to_address } = orderInfoAddressFields;

    const addressFields = {
      to_city: 'city',
      to_state: 'state',
      to_address_name: 'street',
      to_address_complement: 'complement',
      to_address_county: 'neighborhood',
      to_address_number: 'number',
      to_address_zipcode: 'postalCode',
    };

    const address = Object.keys(to_address)
      .filter(key => addressFields.hasOwnProperty(key))
      .reduce(
        (obj, key) => {
          const addressKEY = addressFields[key];
          const addressValue = to_address[key];

          return { ...obj, [addressKEY]: addressValue };
        },
        {
          addressType: 'residential',
        }
      );

    return address;
  }

  async saveData() {
    try {
      const orderInfoAddressFields = this.getAddressFieldsForOrderInfo();
      const address = this.getAddressForOrderForm(orderInfoAddressFields);

      const needStorageInput = document.querySelector('#store-items-change');

      const fromHomeTypeValue = document.querySelector(
        '.js-address-from-home-type'
      );
      const toHomeTypeValue = document.querySelector(
        '.js-address-to-home-type'
      );

      const orderInfoCommonFields = {
        current_step: 'address',
        user_need_storage: needStorageInput.checked,
        home_type: fromHomeTypeValue.value,
        from_home_type: fromHomeTypeValue.value,
        to_home_type: toHomeTypeValue.value,
      };

      const orderInfoFields = {
        ...orderInfoCommonFields,
        ...orderInfoAddressFields,
      };

      const orderInfoFieldsArray = Object.entries(orderInfoFields)
        .reduce((arr, [key, value]) => {
          if (['from_address', 'to_address'].includes(key)) {
            arr = [...arr, ...Object.entries(value)];
          } else {
            arr = [...arr, [key, value]];
          }

          return arr;
        }, [])
        .filter(([key, value]) => value !== '');

      const orderInfoFieldsForCustomData = orderInfoFieldsArray.reduce(
        (obj, [key, value]) => ({ ...obj, [key]: value }),
        {}
      );

      await Promise.all([
        this.cartService.updateCheckoutAddress(address),
        this.cartService.setMultipleCustomDataFields(
          'orderinfo',
          orderInfoFieldsForCustomData
        ),
      ]);
    } catch (err) {
      console.log(err);
      return;
    }
  }

  createCustomSelect() {
    const cardSelects = [
      ...document.querySelectorAll('.address__select-custom'),
    ];

    cardSelects.forEach((cardSelect, index) => {
      const selectElement = cardSelect.querySelector('.select__native');
      const selectCustom = cardSelect.querySelector('.select__custom');
      const customSelectList = cardSelect.querySelector('.select__list');
      const customSelectSelected = cardSelect.querySelector(
        '.select__custom-selected'
      );

      const customSelectInstance = new CustomSelect({
        selectElement,
        customSelectList,
        customSelectSelected,
        customSelectListItemClassName: '',
        customSelectListItemClassNameActive: 'active',
        selectOpenCallback: () => {
          this.customSelectInstances.forEach(customSelecItem => {
            customSelecItem.close();
          });

          const selectListHeight = customSelectList.offsetHeight;

          selectCustom.classList.add('select__custom--open');
          selectCustom.style.maxHeight = `${selectListHeight}px`;
        },
        selectCloseCallback: () => {
          selectCustom.classList.remove('select__custom--open');
          selectCustom.style.maxHeight = 0;
        },
      });

      this.customSelectInstances.push(customSelectInstance);
    });
  }

  async getAddressData() {
    const { customData } = await this.cartService.getCurrent();

    if (!customData) return;

    const storeItemsChangeCheckbox = document.querySelector(
      '#store-items-change'
    );

    customData.customApps
      .filter(customApp => customApp.id === 'orderinfo')
      .forEach(customApp => {
        Object.entries(customApp.fields).forEach(([id, value]) => {
          if (id === 'user_need_storage') {
            if (value.toLowerCase() == 'true') {
              storeItemsChangeCheckbox.checked = value;
            }
          } else if (id.includes('from_') || id.includes('to_')) {
            const inputElement = document.getElementById(id);

            inputElement.value = value;
          }
        });
      });

    this.customSelectInstances.forEach(customSelectInstance => {
      customSelectInstance.setCurrentSelectedItem();
    });
  }

  handledFormChange(form) {
    form.addEventListener('change', ({ target }) => {
      target.classList.remove('address__input--invalid');
    });
  }

  setHandledFormsChange() {
    [...document.forms].forEach(form => this.handledFormChange(form));
  }

  validateForms() {
    const validity = ![...document.forms]
      .map(form => {
        const inputs = [...form.elements]
          .filter(input => input.value === '' && input.required)
          .map(input => {
            input.classList.add('address__input--invalid');

            form.noValidate = true;

            return input;
          });

        if (inputs.length === 0) {
          form.noValidate = false;
        }

        return form.checkValidity() && !form.noValidate;
      })
      .includes(false);

    return validity;
  }

  setHandledButtonUnknownAddressClick() {
    const buttonUnknownAddress = document.querySelector(
      '.js-address-to .address__button'
    );

    buttonUnknownAddress.addEventListener('click', e => {
      buttonUnknownAddress.classList.toggle('address__button--mini-active');

      this.isToAddressUnknown = !this.isToAddressUnknown;

      const addressStreetNameElement = document.getElementById(
        'to_address_name'
      );
      const addressZipcodeElement = document.getElementById(
        'to_address_zipcode'
      );
      const addressCountyElement = document.getElementById('to_address_county');
      const addressNumberElement = document.getElementById('to_address_number');

      [
        addressStreetNameElement,
        addressCountyElement,
        addressNumberElement,
        addressZipcodeElement,
      ].forEach(({ parentElement }) => {
        parentElement.classList.toggle('address__field--required');
      });

      [
        addressStreetNameElement,
        addressCountyElement,
        addressNumberElement,
        addressZipcodeElement,
      ].forEach(element => {
        element.toggleAttribute('required');
        element.toggleAttribute('disabled');
      });
    });
  }
}

document.addEventListener('DOMContentLoaded', e => {
  new Address();
});
